import { Component, Input, OnInit, ViewEncapsulation, ViewChild, ChangeDetectorRef } from '@angular/core';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';

import { Observable, BehaviorSubject } from 'rxjs';
import { filter, map, startWith, debounceTime, takeUntil } from 'rxjs/operators';
import { FormControl } from '@angular/forms';
import { Subscription, Subject } from 'rxjs';

import { MatDrawer } from '@angular/material/sidenav';

import * as moment from 'moment';

@Component({
  selector: 'appointment-profile-list',
  templateUrl: './appointment-profile-list.component.html',
  styleUrls: ['./appointment-profile-list.component.scss'],
  encapsulation  : ViewEncapsulation.None
})
export class AppointmentProfileListComponent implements OnInit {

  isDataLoaded:boolean = true;
  isError:boolean = false;

  @ViewChild('matDrawer', {static: true})
  matDrawer: MatDrawer;

  drawerMode: 'over';
  private _unsubscribeAll: Subject<any>;

  _profilesList:any;
  fullProfilesList:any = [];

  searchData:FormControl;

  search:string = '';
  apiSubscribe:Subscription;

  ItemStartIndex: any = 0;
  ItemEndIndex: any;
  totalItems: any;
  itemLimit: any = 5;

  allData: any;

  message: any;

  constructor() {

    // Set the private defaults
    this._unsubscribeAll = new Subject();

    this._profilesList = new BehaviorSubject(null);
    this.searchData = new FormControl('');

  }

  get profiles$(): Observable<any>{
    return this._profilesList.asObservable();
  }

  getIndexParams(event){

    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;

  }

  getDate(date) {
    return moment(date).format('DD/MM/YYYY hh:mm A');
   }

  onBackdropClicked() {

  }

  onGlSearchFilter() {

  }

  viewProfileUser(id) {

  }

  editProfileUser(id) {

  }

  deleteProfileUser(id) {

  }

  ngOnInit(): void {

    // Load the notifications
    let res = [
      {
        "id": 1,
        "description": "some content",
        "url": "clickmyclass/Test123",
        "zone": "2021-03-22T17:48:29.277Z",
        "availableDays": "Mondays",
        "availbaleInTime": "09:00 am",
        "availbaleOutTime": "05:00 pm",
      }
    ]

    this.fullProfilesList = res;
    this.allData = res;
    this._profilesList.next(res);

    this.profiles$.pipe( filter(res => res != null),
    ).subscribe((res:any) => {

      this.totalItems = res.length;
      this.ItemStartIndex = 0;

      if (this.totalItems > this.itemLimit) {
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

    })

    // Subscribe to search input field value changes
    this.searchData.valueChanges
    .pipe(
      startWith(''),
      map((val) => {

        let newData = this.fullProfilesList.filter(item => {
          for (let field in item) {
            if (item[field] === null || item[field] === undefined) {
              continue;
            }
            if (item[field].toString().toLowerCase().indexOf(val.toString().toLowerCase()) !== -1) {
              return item;
            }
          }
        })

        this._profilesList.next(newData);


      })
    )
    .subscribe();

  }

}
